import $ from 'jquery'
import findIndex from '../../node_modules/lodash-es/findIndex'

/**
 * ------------------------------------------------------------------------
 * Class Definition
 * ------------------------------------------------------------------------
 */

class SelectMultiple {
  constructor(element) {
    this.element = element
    this.btn = element.querySelector('[data-role=btn]')
    this.toggle = element.querySelector('[data-role=select-toggle')
    this.collapses = element.querySelectorAll('[data-role=collapse')
    const input = element.querySelector('[data-role=input]')
    const selectedOptions = this._getSelectedOptions(input)
    const groups = element.querySelectorAll('[data-role=group]')
    const groupValues = {}

    this._addEventListeners()

    groups.forEach((group) => {
      // const counter = group.querySelector('[data-role=counter]')
      const values = group.querySelectorAll('[data-role=value]')

      values.forEach((value) => {
        const isSelected = findIndex(selectedOptions, (o) => {
          const result = o.dataset.id === value.dataset.id
          return result
        })

        groupValues[group.dataset.id] = {}
        groupValues[group.dataset.id][value.dataset.target] = isSelected

        if (isSelected) {
          value.classList.add('active')
        } else {
          value.classList.remove('active')
        }

        value.addEventListener('click', () => {
          groupValues[group.dataset.target][value.dataset.id] = !groupValues[group.dataset.target][value.dataset.id]
          value.classList.toggle('active')
        })
      })
    })
  }

  // Private

  _addEventListeners() {
    this.toggle.addEventListener('click', (event) => {
      event.stopPropagation()
      this.element.classList.toggle('active')
      this.btn.classList.toggle('active')
    })

    this.collapses.forEach((item) => {
      item.addEventListener('click', (event) => {
        event.target.classList.toggle('active')
        // bootstrap jQuery collapse
        $(event.target.dataset.target).collapse('toggle')
      })
    })

    this.element.addEventListener('click', (event) => {
      event.stopPropagation()
    })

    document.addEventListener('click', () => {
      this.element.classList.remove('active')
      this.btn.classList.remove('active')
    })
  }

  _getSelectedOptions(element) {
    // validate element
    if (!element || !element.options) {
      // or null?
      return []
    }

    // return HTML5 implementation of selectedOptions instead.
    if (element.selectedOptions) {
      return element.selectedOptions
    }

    // you are here because your browser doesn't have the HTML5 selectedOptions
    const opts = element.options
    const selectedOptions = []

    for (let i = 0; i < opts.length; i++) {
      if (opts[i].selected) {
        selectedOptions.push(opts[i])
      }
    }

    return selectedOptions
  }
}

export default SelectMultiple
